.Home {
    padding-top: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

@media (min-width: 768px) {
    .Home {
        width: 660px;
        margin: 0 auto;
    }
}

@media (min-width: 1000px) {
    .Home {
        width: 850px;
        margin: 0 auto;
    }
}

.title_box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0 auto 20px;
}

@media (min-width: 768px) {
    .title_box {
        justify-content: flex-start;
    }
}

.home_title {
    display: inline-block;
    margin-left: 10px;
    font-size: 42px;
    font-weight: bold;
    font-family: 'Montserrat-med';
}

.info_box_title {
    font-size: 29px;
    margin-bottom: 20px;
}

.span_1 {
    color: dodgerblue;
}

.span_2 {
    color: yellowgreen;
}

.span_3 {
    color: orangered;
}

.span_4 {
    color: greenyellow;
}

.span_5 {
    color: orange;
}

.span_6 {
    color: purple;
}

.works {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    margin-bottom: 40px;
}

.works_title {
    margin-bottom: 10px;
    font-family: Montserrat-med;
    font-size: 29px;
    font-weight: normal;
}

.works_link {
    margin-bottom: 10px;
    text-decoration: none;
    color: #00aef3;
}